
#portfolio {

	.container {

		width: 100%;
		height: 100%;

		position: relative;
		overflow: auto;

		@include sm {

			width: calc(100% + 30px);
			height: calc(100% + 30px);

			left: -15px; }

		.item {

			display: block;
			float: left;

			margin: 5px 0;

			@include sm {

				margin: 0; }

			&.one {

				width: 100%;

				@include sm {

					width: 25%; } }

			&.two {

				width: 100%;

				@include sm {

					width: 50%; } }

			&.three {

				width: 100%;

				@include sm {

					width: 75%; } }

			&.four {

				width: 100%;

				@include sm {

					width: 100%; } }

			.element {

				width: 100%;
				height: 100%;

				@include sm {

					width: calc(100% - 30px);
					height: calc(100% - 30px);

					margin: 15px; }

				img {

					width: 100%;
					height: auto; } } } } }
